@import url(https://use.typekit.net/nqj6gya.css);
/* Link to Adobe Fonts containting Acumin, Neuzeit Grotesk, Franklin Gothic URW, and Mr Eaves Sans */

body {
  margin: 0;
  font-family: acumin-pro, sans-serif;
  font-style: normal;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  position: relative;
}

code {
  font-family: neuzeit-grotesk, sans-serif;
  font-style: normal;
  font-weight: normal;
}

h1 {
  font-family: neuzeit-grotesk, sans-serif;
  font-style: normal;
  font-weight: normal;
}

h2, h3 {
  font-family: neuzeit-grotesk, sans-serif;
  font-style: normal;
  font-weight: normal;
}

h5 {
  font-family: neuzeit-grotesk, sans-serif;
  font-style: normal;
  font-weight: normal;
  text-align: center;
}

p, h6 {
  font-family: franklin-gothic-urw, sans-serif;
  font-style: normal;
  font-weight: normal;
  text-align: justify;
}

.btn-flat {
  background-color: white;
}

.btn-xxl {
  font-size: xx-large;
  font-weight: bolder;
}

.progress.turquoise .progress-bar {
  background-color: #037963;
}

caption-side {
  caption-side: top;
}

.App {
  text-align: center;
  background-color: black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
